<template>
  <div>
    <p>this is userList</p>
      <button v-on:click='getUsers'></button>

      <ul>
        <li v-for="user in users">
          <span>{{user.id}}</span>
          <div>
            <b-dropdown id="ddown1" text="메뉴" class="m-md-2">
              <b-dropdown-item @click="UserMenu(user.id,user._id)">메시지보내기</b-dropdown-item>
              <b-dropdown-item>차단</b-dropdown-item>
              <b-dropdown-divider></b-dropdown-divider>
            </b-dropdown>
          </div>
        </li>
      </ul>
  </div>
</template>

<script>
  import io from 'socket.io-client';


  export default {
    name: "UsersVue",
    data() {
      return {
        users: [],
        socket: io('localhost:3000'),

      }
    },
    methods: {
      getUsers: function (event) {
        this.$http.post('/users', {
          user: this.user
        })
          .then(
            (response) => {
              console.log(response);
              console.log(response.data.length);
              this.users = response.data;

            },
            (error) => {
              alert(error.response.data.error)
            }
          )
          .catch(error => {
            alert(error)
          })
      },
      UserMenu(id, oid) {
        // 메시지 보내는 창 띄우기
        console.log(`oid: ${oid} id : ${id}`);
        let senderOID = localStorage.getItem('userOid');
        let receiverOID = oid;
        console.log(`serderOID : ${senderOID} receiverOID : ${receiverOID}`);

        let newMsg = prompt('메시지를 입력하세여');  // 처음 보낼 메시지 입력
      }
    },
    mounted() {
      this.socket.on('connect', ()=>{
        console.log(`connected`);

      });
    }
  }
</script>

<style scoped>
</style>



